// "currQuery.page = '1';" –¥–ª—è –∫–æ—Ä–µ–∫—Ç–Ω–æ–≥–æ —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ –ø—Ä–∏ —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏.
// –≤ refs.js –¥–æ–±–∞–≤–ª–ª–µ–Ω –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä "paginationContainer" –¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏.

export { handleMediaChange };
